import { Component } from '@angular/core';
import { NavController, LoadingController } from 'ionic-angular';
import { HttpClient } from '@angular/common/http';

import { HTTP } from '@ionic-native/http';
import { OneSignal } from '@ionic-native/onesignal';



@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  public error : any = '';
  public p : any = [];
  public mode : any = 0;
  public myname : any = '';
  public started : any = 0;
  public messages : any[];
  public txt : any = '';
 public tick : any = './assets/img/tick.png';

  public blank : any = './assets/img/blank.png';


  constructor(public navCtrl: NavController, public loadingCtrl : LoadingController, private http: HttpClient, public nhttp : HTTP, private oneSignal: OneSignal) {
    this.p = { email : '', pass : '', empcode : '', timezn : '', locn : 'Unknown', lat : '', lng : '', me : 0, job : 0};
    this.messages = [];
  }

 getpushinfo()
  {
     this.oneSignal.startInit('07718501-db1c-4b51-ade6-85b3155bab32', '126894144681'); 
     this.oneSignal.handleNotificationReceived().subscribe(() => {
   this.pinme();
});

     this.oneSignal.inFocusDisplaying(this.oneSignal.OSInFocusDisplayOption.Notification);
     
     this.oneSignal.endInit();
     let i = this.oneSignal.getIds();
     i.then( data => {

        let w = window.localStorage.getItem('me');
        window.localStorage.setItem('pushid',data.userId);

        this.http.get('http://jaydenkur.com.au/giovenco/server/pushtoken.php?me='+w+'&pushid='+data.userId).subscribe(data2	=>	{	
        });
     });
  }     


  ionViewDidEnter() {
     let that = this;
     let me = window.localStorage.getItem('me');
     if (!me) me='';
     if (me) {
       this.mode=1;
       this.myname = window.localStorage.getItem('myname');
       this.getpushinfo();
       this.pinme();
     }
     else this.mode=0;
  }

  

  checkeml()
  {
         this.error='';
        let that = this;
	this.http.get('http://jaydenkur.com.au/giovenco/server/checkeml.php?eml='+this.p.email+'&pwd='+this.p.pass).subscribe(data	=>	{	
       		let s = JSON.stringify(data);
                let d = JSON.parse(s);
                if (d.num) {
                  window.localStorage.setItem('myname',d.myname);
                  window.localStorage.setItem('me',d.num); 
                  this.mode=1; 
                  this.myname=d.myname;
                  that.getpushinfo();  
                  that.pinme();
                }
                else {
                  this.error='Incorrect - Please Try Again';
                }
        });


  }

  pinme()
  { let that=this;
    this.http.get('http://jaydenkur.com.au/giovenco/server/mystate.php?me='+window.localStorage.getItem('me')+'&rnd='+Math.random()).subscribe(data	=>	{	
       		let s = JSON.stringify(data);
                let d = JSON.parse(s);
                that.messages = d.messages;
        });
  }

  sendtxt()
  { let that=this;
    this.http.get('http://jaydenkur.com.au/giovenco/server/mystate.php?me='+window.localStorage.getItem('me')+'&txt='+encodeURIComponent(this.txt)+'&rnd='+Math.random()).subscribe(data	=>	{	
       		let s = JSON.stringify(data);
                let d = JSON.parse(s);
                that.messages = d.messages;
                that.txt=''; 
        });
  }

 logout()
 { window.localStorage.removeItem('me');
   window.localStorage.setItem('myname','');
   this.mode=0;
 }
 


}



